.container {
    width: 550px;
    min-height: 350px;
    border-radius: 7px;
    border: none;
    box-shadow: 0px 1px 12px 6px rgba(255, 146, 52, 1);
    margin: 110px auto;
    transition: all ease 0.3s;
    background: #35d0ba;
    color: #fff;
    padding: 10px;
}

.img {
    padding: 20px;
    border-radius: 50%;
}

.container-text {
    padding: 10px;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 250px;
    width: 500px;
}

.author {
    float: right;
}

.footer-container {
    padding: 10px;
    display: flex;
    justify-content: space-between;
}

.button {
    width: 100px;
    height: 50px;
    border-radius: 7px;
    padding: 5px;
    background: black;
    color: white;
    border: none;
    outline: none;
    cursor: pointer;
}

.button:focus {
    outline: none;
}

@media screen and (max-width: 700px) {
    .container {
        width: 90%;
        margin-left: 5%;
    }
    .container-text {
        width: 80%;
        margin-left: 10%;
    }
}